*{
  margin: 0;
  padding: 0;
}
.header{
  color: #104994;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10%;
  height: 8rem;
  box-shadow: 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}

.header .header-left{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 27%;
}

.header .header-left .logo{
  display: flex;
  align-items: center;
  justify-content: center;

}

.header .header-left .logo img{
  height: 5.5rem;
  width: 5.5rem;
}

.header .header-left .name h1{
  font-size: 45px;
  font-weight: 300;
}

.header .header-rigth button{
  border-style:  none;
  background-color: #104994;
  height: 3rem;
  width: 10rem;
  border-radius: 5px;
  color: white;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  transition: all .6s;
}

.header .header-rigth button:hover{
  border: solid 1px #104994;
  color: #104994;
  background-color: white;
}

.info{
  display: flex;
  align-items: end;
  justify-content: center;
  height: 30rem;
}

.info .all{
  width: 98%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: url(../src/assets/Conjunto.jpg) ;
  background-size: cover; 
  background-position: center;

}

.info .all .title h2{
  font-size: 60px;
  font-weight: 200;
  color: white;
  text-align: center;
}

.info .all .button button{
  border-style: none;
  background-color: white;
  height: 3rem;
  width: 10rem;
  border-radius: 5px;
  color: #104994;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  transition: all .6s;

}

.info .all .button button:hover{
  scale: 1.2;
}

.Space{
  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-evenly;
  align-items: end;
  height: 22rem;
}
.Space div{
  border-radius: 10px;
  width: 20rem;
  height: 9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: all .4s;
}
.Space div:hover{
  scale:1.1;
}

.Space div h3{
  color: white;
  font-size: 23px;
  font-weight: 400;
}
.Space div p{
  color: white;
  font-size: 18px;
  font-weight: 200;
}

.Space .area1{
  background-image: url(../src/assets/Casas.jpg);
  background-size: cover; 
  background-position: center;
}
.Space .area2{
  background-image: url(../src/assets/Manzanas.jpg);
  background-size: cover; 
  background-position: center;
}
.Space .area3{
  background-image: url(../src/assets/parking.jpg);
  background-size: cover; 
  background-position: center;
}
.Space .area4{
  background-image: url(../src/assets/play.jpg);
  background-size: cover; 
  background-position: center;
}
.Space .area5{
  background-image: url(../src/assets/bbq.jpg);
  background-size: cover; 
  background-position: center;
}
.Space .area6{
  background-image: url(../src/assets/admin.jpg);
  background-size: cover; 
  background-position: center;
}

  .location{
    height: 35rem;
    background-color: #c4c4c4;
    display: flex ;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
  }
  .location .text{
    background-color: white;
    width: 40%;
    height: 80%rem;
    display: flex ;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 2%;
    border-radius: 6px;
    box-shadow: 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
  }
  .location .text .title h3{
    font-size: 25px;
    font-weight: 400;
  }
  .location .text .content p{
    font-size: 18px;
    font-weight: 300;
  }
  .location .img{
    width: 40%;
  }
  .location .img img{
    width: 80%;
  }

  .footer{
    display: grid;
    grid-template-columns: auto auto auto; 
    align-items: center;
    justify-content: space-evenly;
  }
  .footer .section1{
    width: 100%;
  }
  .footer .section2{
    width: 100%;
  }  
  .footer .section3{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer .section3 .title{
    font-size: 21px;
    font-weight: 400;
  }
  .footer .section3 .icons{
    width: 80%;
    display: flex;
    justify-content: space-evenly;
  }

  .footer div ul li{
    list-style: none;
  }
  .footer div ul li p{
    font-size: 15px;
    font-weight: 300;
    line-height: 30px;
  }

  .copyright{
    border-top-style: solid ;
    border-width: 1px;
    width: 90%;
    margin: auto;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
  }
/*------------------RESPONSIVE--------------------------------*/


@media screen and (max-width: 960px) {

  .header .header-left{
    width: 50%;
    transition: .4s;
  }
  .info .all .title h2{
    font-size: 40px;
    transition: .4s;
    }

  .Space{
    grid-template-columns: auto auto;
    height: 35rem;
    transition: .4s;
  }
  .location .text .title h3{
    font-size: 20px;
  }
  .location .text .content p{
    font-size: 15px;
  }
  .footer{
    grid-template-columns: auto auto;
    height: 19rem;
  }
}

@media screen and (max-width: 660px) {
  .Space{
    grid-template-columns: auto;
    height: 60rem;
  }
  .location{
    height: 48rem;
    flex-direction: column;
    transition: .4s;

  }
  .location .text .title h3{
    font-size: 20px;
  }
  .location .text .content p{
    font-size: 15px;
  }
  .location .img img{
    height: 100%;
  }
}
@media screen and (max-width: 600px) {
  .header .header-left .logo{
    width: 0;
    height: 0;
  }
  .header .header-left .logo img{
    width: 0;
    height: 0;
  }

}

@media screen and (max-width: 480px) {
  .header .header-left{
    justify-content: start;
  }
  .header .header-left .name h1{
    font-size: 30px;
  }
  .location{
    height: 53rem;
    transition: .4s;
  }
  .location .text .title h3{
    font-size: 18px;
  }
  .location .text .content p{
    font-size: 12px;
  }
  .location .img img{
    width: 140%;
    margin-left: -30px;
  }
  .footer{
    grid-template-columns: auto;
    height: 24rem;
  }
}